

@keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }

  .Loading {
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top: 4px solid var(--color3); 
    animation: spin 1s linear infinite;

    display: flex;
    align-self: center;
    margin: auto;
  }